import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";
import styles from "./Col1.module.css";

type Col1Type = {
  paris?: string;
  component?: string;
  greeceImage?: string;

  /** Style props */
  detailsWidth?: Property.Width;
  componentDisplay?: Property.Display;
  componentWidth?: Property.Width;
  fromLeft?: Property.Left;

  /** Action props */
  onCol1Click?: () => void;
};

const Col1: FunctionComponent<Col1Type> = ({
  onCol1Click,
  paris,
  detailsWidth,
  component,
  componentDisplay,
  componentWidth,
  fromLeft,
  greeceImage,
}) => {
  const detailsStyle: CSS.Properties = useMemo(() => {
    return {
      width: detailsWidth,
    };
  }, [detailsWidth]);

  const divStyle: CSS.Properties = useMemo(() => {
    return {
      display: componentDisplay,
      width: componentWidth,
    };
  }, [componentDisplay, componentWidth]);

  const fromStyle: CSS.Properties = useMemo(() => {
    return {
      left: fromLeft,
    };
  }, [fromLeft]);

  return (
    <button className={styles.col1} onClick={onCol1Click}>
      <div className={styles.pariscard}>
        <img className={styles.parisimageIcon} alt="" src={greeceImage} />
        <div className={styles.destinationDetails}>
          <b className={styles.paris}>{paris}</b>
          <div className={styles.details} style={detailsStyle}>
            <div className={styles.div} style={divStyle}>
              {component}
            </div>
            <div className={styles.from} style={fromStyle}>
              from
            </div>
          </div>
        </div>
      </div>
    </button>
  );
};

export default Col1;
